"use client"

import * as React from "react"
import { Loader2 } from "lucide-react"
import { useRouter } from "next/navigation"

import { cn } from "@/lib/utils"
import { useAuth } from "@/features/auth"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import {
  Field,
  FieldDescription,
  FieldGroup,
  FieldLabel,
} from "@/components/ui/field"
import { Input } from "@/components/ui/input"

export function LoginForm({
  className,
  ...props
}) {
  const router = useRouter()
  const { signIn, status, error } = useAuth()
  const [email, setEmail] = React.useState("")
  const [name, setName] = React.useState("")
  const [formError, setFormError] = React.useState(null)

  const isLoading = status === "loading"

  const handleSubmit = async (event) => {
    event.preventDefault()
    setFormError(null)

    try {
      await signIn({ email, name })
      router.push("/")
    } catch (err) {
      setFormError(err?.message ?? "로그인에 실패했습니다.")
    }
  }

  React.useEffect(() => {
    if (error) {
      setFormError(error.message)
    }
  }, [error])

  return (
    <div className={cn("flex flex-col gap-6", className)} {...props}>
      <Card>
        <CardHeader>
          <CardTitle>OpenID SSO</CardTitle>
          <CardDescription>
            사내 OpenID 공급자로 싱글 사인온을 진행합니다. 입력값은 더미 API에서 그대로 사용됩니다.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <FieldGroup>
              <Field>
                <FieldLabel htmlFor="name">이름</FieldLabel>
                <Input
                  id="name"
                  name="name"
                  placeholder="홍길동"
                  value={name}
                  onChange={(event) => setName(event.target.value)}
                  autoComplete="name"
                />
                <FieldDescription>표시명은 NavUser 등에서 노출됩니다.</FieldDescription>
              </Field>
              <Field>
                <FieldLabel htmlFor="email">Email</FieldLabel>
                <Input
                  id="email"
                  name="email"
                  type="email"
                  placeholder="m@example.com"
                  value={email}
                  onChange={(event) => setEmail(event.target.value)}
                  autoComplete="email"
                  required
                />
                <FieldDescription>실제 OpenID 서버 전환 시 login_hint 용도로 활용됩니다.</FieldDescription>
              </Field>
              <Field className="space-y-2">
                <Button type="submit" disabled={isLoading} className="w-full">
                  {isLoading ? (
                    <>
                      <Loader2 className="mr-2 size-4 animate-spin" aria-hidden="true" />
                      로그인 중...
                    </>
                  ) : (
                    "OpenID SSO로 로그인"
                  )}
                </Button>
                {formError ? (
                  <p className="text-sm text-destructive">{formError}</p>
                ) : null}
                <FieldDescription className="text-center">
                  이 데모는 항상 성공하도록 구성된 더미 인증 API를 호출합니다.
                </FieldDescription>
              </Field>
            </FieldGroup>
          </form>
        </CardContent>
      </Card>
    </div>
  )
}
